import styles from "../common/formsControls/style.module.css";
import { Input } from "../common/formsControls/FormsControl";
import { connect } from "react-redux";
import { login } from "../../redux/auth-reducer";
import { Navigate } from "react-router-dom";
import { useForm } from "react-hook-form"; 


const LoginForm = ({ onSubmit, error }) => {
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onTouched"
    });
    debugger


    return (
        <form onSubmit={handleSubmit(onSubmit)}> 
            <div>
                <Input 
                    {...register("email", { 
                        required: "Email field is required", 
                        maxLength: { value: 30, message: "Max length is 30 symbols" } 
                    })} 
                    type="email" 
                    placeholder="Email"
                    error={errors.email}
                />
            </div>

            <div>
                 <Input 
                    {...register("password", { 
                        required: "Password field is required",
                        maxLength: { value: 30, message: "Max length is 30 symbols" } 
                    })} 
                    type="password" 
                    placeholder="Password"
                    error={errors.password}
                />
            </div>

            
            <div className={styles.checkbox}>
                <Input {...register("rememberMe")} type="checkbox" />
                <span>remember me</span>
            </div>
            
            {error &&
                <div className={styles.formSummaryError}>
                    {error}
                </div>
            }
            <div>
                <button type="submit">Login</button>
            </div>
        </form>
    );
}





// ... остальной код компонентов Login и mapStateToProps остается прежним ..

const Login = ({ login, isAuth, error }) => {
    
    const onSubmit = (dataForm) => {
        login(dataForm.email, dataForm.password, dataForm.rememberMe);
    };

    if (isAuth) {
        return <Navigate to={"/profile"} />;
    }

    return (
        <div>
            <h1>Login</h1>
            <LoginForm error={error} onSubmit={onSubmit} /> 
        </div>
    );
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    error: state.auth.error
});

export default connect(mapStateToProps, { login })(Login);